.App-header {
    /*width: 100vw;*/
    font-size: 12.5vmin;
    color: #495057;
}

.App-footer {
    display: flex;
    min-height: 7%;
    width: 100%;
    justify-content: flex-end;
    align-items: center;
    color: #282c34;
    background-color: #e2d79c;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: space-between;
}

.App-link {
    color: #61dafb;
}

button, a {
    font-size: 3vmin;
    -webkit-appearance: none;
    border: 0;
    background: transparent;
    text-decoration: none;
}

html, body {
    margin: 0;
    padding: 0;
    overflow: hidden
}

svg {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
}

.jumbo {
    color: inherit !important;
    font-size: inherit !important;
}

.policy {
    font-family: 'Alegreya';
    font-size: 16px;
    background: white;
    transform: translate(50%, 0%);
    color: #757373;
    padding: 50px;
    width: 50%;
    overflow-y: scroll;
    height: 95%;
    position: absolute;
}

.product_root {
    width: 100%;
    height: auto;
    align-self: center;
    background: #e3e3e3;
}

.product_name {
    align-self: center;
    font-size: 50px;
    font-family: "Alegreya", serif;
}

.product_desc {
    font-size: 20px;
    font-family: "Alegreya", serif;
    height: 50%;
    margin: 30px;
}

.carousel-root {
    display: inline-flex;
    height: 90vh;
    width: 100%;
    place-content: center;
    place-items: center;
    place-self: center;
}

.main_carousel > .carousel.carousel-slider {
    overflow: visible;
    width: 80% !important;
}

.carousel {
    width: -webkit-max-content !important;
    width: -moz-max-content !important;
    width: max-content !important;
}

.slide {
    background: transparent !important;
    display: flex;
    place-content: center;
    flex-flow: row !important;
}

.carousel .control-next.control-arrow:before {
    border-left: 25px solid #000;
}

.carousel .control-prev.control-arrow:before {
    border-right: 25px solid #000;
}

.product_header {
    display: flex;
    justify-content: center;
    grid-gap: 14px;
    gap: 14px;
}

.logo {
    width: 7% !important;
    object-fit: contain;
}
a{
    font-size: inherit;
}
.slide_img {
    height: 50vh;
    width: auto !important;
}

@media only screen and (max-width: 768px) {
    .policy {
        width: calc(100vw - 70px);
        height: calc(100vh - 60px);
        transform: translate(0%, 0%);
        padding: 31px;
        margin: 20px 30px;
        display: inline-block;
        position: relative;
    }
}

@-webkit-keyframes grow {
    from {
        transform: scale(1);
    }
    to {
        transform: scale(4.333);
    }
}

@keyframes grow {
    from {
        transform: scale(1);
    }
    to {
        transform: scale(4.333);
    }
}

